@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 41px 0 118px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
	        padding: 4px 0 0 185px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 49px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 138px;
        height: 138px;
        float: left;

        img {
            display: block;
            border-radius: 138px;
        }
    }

    .eltdf-comment-text {
	    padding: 26px 0 0 174px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
	        font-family: $default-heading-font;
	        font-weight: 600;
            font-size: 14px;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }
	    
	    .eltdf-reply-wrapper {
		    margin-right: 10px;
	    }
	    
	    .eltdf-reply-icon {
		    vertical-align: middle;
	    }

        .eltdf-comment-name {
            float: left;
            margin: 0;
	        letter-spacing: 0.05em;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 14px;
	        font-family: $default-heading-font;
	        color: #434343;
        }

        .eltdf-text-holder p {
	        margin: 7px 0 18px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
	border-top: 1px solid #ccc;
    
    .eltdf-comment-form-inner {
	    padding: 101px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 46px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control-wrap {
	display: inline-block;
	width: 100%;
}
.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}
.comment-form,
.wpcf7-form {
	label {
		@extend %label-style;
	}
}
.wpcf7-form {
	p {
		line-height: 0;
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	padding: 0 !important;
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    padding: 0 !important;
    color: #080808;
    background-color: transparent;
    border: 0;
    vertical-align: middle;
	cursor: pointer;

	&:hover ~ i{
        @include eltdfTransform(translateX(3px));
	}
}

.eltdf-cf7-submit-holder {
    position: relative;
    display: inline-block;

    .eltdf-icon-ion-icon {
        position: relative;
        top: 1px;
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 8px;
        color: #080808;
        @include eltdfTransitionTransform(.2s cubic-bezier(0.215, 0.61, 0.355, 1));
    }

    .ajax-loader {
        position: absolute;
        left: 100%;
        top: 8px;
    }
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.eltdf-comment-field {
	display: inline-block;
	float: left;
	width: 32%;
	margin-right: 2%;
	&.eltdf-last-field {
		margin-right: 0;
	}
	@include ipad-portrait {
		width: 100%;
		margin-right: 0;
	}
}

.eltdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.eltdf-slider-is-moving {
		
		.owl-item {
			
			.eltdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.eltdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			color: #231f20;
			width: 56px;
			height: 56px;
			border: 1px solid #080808;
			text-align: center;
			line-height: 56px;
			position: absolute;
			top: 50%;
			z-index: 3;
			@include eltdfTransform(translateY(-50%));
			@include eltdfTransition(color .2s ease-out);

			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 32px;
				line-height: 1;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 0;
		}
		
		.owl-next {
			right: 0;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 1px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
    #Rev Slider - start
    ========================================================================== */

    #eltdf-main-rev-slider .rev_slider_wrapper {
        background: transparent url('../img/preload_pattern.png') repeat scroll inherit !important;
        @include eltdfAnimation(preload-background 20s infinite ease 0s);
    }

/* ==========================================================================
    #Rev Slider - end
    ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.eltdf-content {
			height: calc(100vh - 32px);
		}
	}

    .eltdf-content-inner {
	    @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
	    @include eltdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .eltdf-404-title {
            margin: 0;
        }
	
	    .eltdf-404-subtitle {
            margin: 16px 0 0;
        }
	    
	    .eltdf-404-text {
		    margin: 10px 0 0;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
	
	        &.eltdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include ipad-landscape {
		
		.eltdf-content {
			height: 100%;
			min-height: 100vh;
		}
		
		&.admin-bar {
			
			.eltdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 45px;
        font-size: 23px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);
    }

    span span {
        display: block;
        line-height: inherit;

        &:before {
            display: inline-block;
            @include eltdfTransitionTransform(.2s cubic-bezier(0.215, 0.61, 0.355, 1));
        }
    }

    &:hover  {
        span span:before {
            @include eltdfTransform(translateY(-3px));
        }
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
	
    .eltdf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */