// widgets spacing
$widget-bottom-margin: 45px;
$widget-title-margin-bottom: 15px;
$title-border-style : 1px solid #cccccc;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 21px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: #262626;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;
$link-text-transform: uppercase;

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
        
        &.widget_text {
            .textwidget {
                width: 75%;
            }
        }
        
        &.widget_eltdf_instagram_widget {
            .eltdf-widget-title-holder {
                margin-bottom: 38px;
            }
        }
        
        &.eltdf-blog-list-widget {
            .eltdf-widget-title-holder {
                margin-bottom: 33px;
            }
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .eltdf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.eltdf-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';
}