// widgets spacing
$widget-bottom-margin: 10px;
$widget-title-margin-bottom: 16px;
$title-border-style : none;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 5px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #fff;

// links
$widget-link-color: #eeeeee;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;
$link-text-transform: none;

footer {
    @import 'widgets/default-wordpress-widgets-flat';
    .widget {
        color: #8e8e8e;
        .eltdf-widget-title-holder {
            .eltdf-widget-title {

            }
        }
        
        &.eltdf-blog-list-widget {
            .eltdf-blog-list-holder.eltdf-bl-minimal {
                .eltdf-post-info-date a {
                    color: rgba(#fff, 0.4);
                }
                .eltdf-post-title a {
                    text-transform: uppercase;
                }
            }
        }
        
        &.widget_eltdf_instagram_widget {
            .eltdf-widget-title-holder {
                margin-bottom: 20px;
            }
        }
    }
}